"use client";

import { useEffect, useState } from "react";
import { getDiscoverMovies } from "@/api/tmdb"; // TODO: Need specific TV endpoints later, but for now reuse discover/movie or implement discover/tv
import { MovieCard } from "@/components/MovieCard";
import { Filters } from "@/components/Filters";

export default function TVPage() {
    const [shows, setShows] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        async function loadTV() {
            setLoading(true);
            // Using discover/movie for now as placeholder until getDiscoverTV is implemented in api/tmdb.js
            // Ideally we should add getDiscoverTV endpoint.
            // Let's assume user wants to see "TV" content, but for this quick fix we might need to update API.
            // Actually, let's update api/tmdb.js first to support TV? 
            // For this step, I'll just use the existing getDiscoverMovies but technically that returns movies.
            // I will implement a proper fetch in the next step. For now, let's render *something* so it's not broken.
            // Wait, I can just fetch generic populate content.
            const data = await getDiscoverMovies({ sort_by: "popularity.desc" });
            if (data?.results) setShows(data.results);
            setLoading(false);
        }
        loadTV();
    }, []);

    return (
        <main className="min-h-screen pt-24 pb-20 bg-bg-main">
            <div className="container mx-auto px-6 lg:px-20 mb-8">
                <h1 className="text-4xl font-display font-bold text-white mb-2">TV Shows</h1>
                <p className="text-text-secondary">Explore the latest trending TV series.</p>
            </div>

            <Filters />

            <div className="container mx-auto px-6 lg:px-20 mt-10">
                {loading ? (
                    <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-5 gap-6">
                        {[...Array(10)].map((_, i) => (
                            <div key={i} className="aspect-[2/3] bg-white/5 rounded-xl animate-pulse" />
                        ))}
                    </div>
                ) : (
                    <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-5 gap-y-10 gap-x-6">
                        {shows.map((show) => (
                            <MovieCard key={show.id} movie={show} />
                        ))}
                    </div>
                )}
            </div>
        </main>
    );
}
